body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: Consolas, "Courier New", monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

html {
  background-image: -webkit-linear-gradient(315deg, #649cf0 0%, #1d62d1 100%);
  background-image: linear-gradient(135deg, #649cf0 0%, #1d62d1 100%);
}
body {
  background-size: auto 50%;
  font-size: calc(.8rem + 1vh);
  font-family: Lato;
  display: flex;
  align-items: center;
  justify-content: center;
}

#main {
  color: white;
  width: 75vw;
  height: 100vh;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

#main h1,
h2 {
  text-align: center;
  padding: 1rem;
}

.clock {
  margin-top: 2vh;
}

.card {
}

.card ul {
  height: 60vh;
  margin: 1vh 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.card li {
  margin: 1.2rem 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.select {
  width: 100%;
  color: black;
  margin-bottom: 2vh;
}

.select select {
  width: 100%;
  padding: 0.8rem;
  border-radius: 2px;
}

.name {
  text-transform: capitalize;
}
.time {
}

.difference {
  color: red !important;
}

li.next-prayer {
  font-weight: 700;
  background-color: white;
  color: black;
  padding: 0.5rem 0.4rem 0.5rem;
  border-radius: 2px;
}

/* Media Queryes */

@media  (min-width: 500px) and (max-width: 600px) {
  #main {
    width: 70vw;
    font-size: calc(1rem + 1vh);
  }
}
@media  (min-width: 600px) and (max-width: 700px) {
  #main {
    width: 65vw;
    font-size: calc(1.2rem + 1vh);
  }
}
@media  (min-width: 700px) and (max-width: 800px) {
  #main {
    width: 60vw;
    font-size: calc(1.4rem + 1vh);
  }
}
@media  (min-width: 800px) and (max-width: 1200px) {
  #main {
    width: 55vw;
    font-size: calc(1.6rem + 1vh);
  }
}
@media  (min-width: 1200px) {
  #main {
    width: 40vw;
    font-size: 1.8rem;
  }
}

.spinner_spinner__13LQm {
  /* display: inline-block; */
  width: 64px;
  height: 64px;
  flex: 1 1;
}

.spinner_container__3X5jo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.spinner_spinner__13LQm:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: spinner_spinner__13LQm 1.2s linear infinite;
          animation: spinner_spinner__13LQm 1.2s linear infinite;
}
@-webkit-keyframes spinner_spinner__13LQm {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes spinner_spinner__13LQm {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

